export function querygetVideoFileInfo(data,cancelToken) { //
    return request({
        url: data.url,
        method: 'get',
        data:data,
        cancelToken: cancelToken
    })  
}
export function getVideoIpPort(data,cancelToken) { //
    return request({
        url: data.url,
        // url:'/proxy/3/1',
        method: 'post',
        params:data.data,
        cache:false,
        cancelToken: cancelToken
    })
}