//
function dateGetNextYear(){
	var today = new Date();
	var y,m,d;
	d = today.getDate();
	m = today.getMonth();
	y = today.getFullYear();
	y ++;//1
	if(m==1 && d==29) {	//0
		return new Date(y,3,1);//31
	} else {
		return new Date(y,m,d);
	}
}

//
function dateGetNextMonth(){
	var today = new Date();
	var y,m,d;
	d = today.getDate();
	m = today.getMonth() + 1;//0
	y = today.getFullYear();
	  
	m++;
	if( m > 12){//11211
		m=1;	y++;
	}
	 
	if( !dateCheckDateString(y + '-' + m + '-' + d) ){//11
		m++;
		if( m > 12){//11211
			m=1;y++;
		}
		d=1;
	}
	var nextToday = new Date(y, m, d);
	return nextToday;
}

//
function dateGetNextMulMonth(day, count) {
	var today = day;
	var y,m,d;
	d = today.getDate();
	m = today.getMonth() + 1;//0
	y = today.getFullYear();
	  
	m += count;
	if( m > 12){//11211
		y += parseInt(m/12);
		m = m%12;	
		if (m == 0) {
			m = 12;
		}
	}
	if( !dateCheckDateString(y + '-' + m + '-' + d) ){//11
		m++;
		if( m > 12){//11211
			y += parseInt(m/12);
			m = m%12;	
			if (m == 0) {
				m = 12;
			}
		}
		d=1;
	}
	var nextToday = new Date(y, m - 1, d);
	return new Date(Date.parse(nextToday) - 86400000);
}

//
export function dateGetNextMulDay(day, count) {
	return new Date(Date.parse(day) + count * 86400000);
}

//,--
function dateCheckDateString(str) {
	var r = str.match(/^(\d{1,4})(-|\/)(\d{1,2})\2(\d{1,2})$/); 
	if (r==null) {
		return false;
	} 
	
	var d= new Date(r[1], r[3]-1, r[4]); 
	return (d.getFullYear()==r[1]&&(d.getMonth()+1)==r[3]&&d.getDate()==r[4]);
}
   
//,-
function monthCheckDateString(str) {
	var r = str.match(/^(\d{1,4})(-|\/)(\d{1,2})\2(\d{1,2})$/); 
	if (r==null) {
		return false;
	} 
	
	var d= new Date(r[1], r[3]-1); 
	return (d.getFullYear()==r[1]&&(d.getMonth()+1)==r[3]);
}

//
function dateGetLastMonth(){
	var today = new Date();
	var y,m,d;
	d = today.getDate();
	m = today.getMonth() + 1;//0
	y = today.getFullYear();
	  
	m--;
	if( m < 1){//11211
		m=12;	y--;
	}
	if( !dateCheckDateString(y + '-' + m + '-' + d) ){//
		var maxDay = getCountDays(dateStrDate2MonthDate(y + '-' + m));//
		if(d > maxDay) {
			d = maxDay;
		}else {
			d=1;
		}
	}
	var nextToday = new Date(y, m - 1, d);
	return nextToday;
}

