//  10:00:23	
function dateIsValidTime(str)
{
	var a = str.match(/^(\d{1,2})(:)?(\d{1,2})\2(\d{1,2})$/);
	if (a == null) { 
		return false;
	}
	
	if (a[1]>24 || a[3]>60 || a[4]>60)
	{
		return false;
	}
	
	return true;
}

