function dateCompareStrLongTimeRange(strB, strE, day) {
	var dB = dateStrLongTime2Date(strB);
	var dE = dateStrLongTime2Date(strE);
	var span = dE.getTime() - dB.getTime();
	if ( span > (day*1000*60*60*24) ) {
		return false;
	} else {
		return true;
	}
}

//YYYY-MM-dd
