//IEnew Date()  
function NewDate(str){
    if(!str){
        return 0;
    }
    arr=str.split(" ");
    d=arr[0].split("-");
    t=arr[1].split(":");
    var date = new Date();
    date.setFullYear(d[0], d[1] - 1, d[2]);
    date.setHours(t[0], t[1], t[2], 0);
    return date;
}




//
function dateIsTimeout(last, interval) {
	var date = new Date();
	var nowTime = date.getTime();
	var lastTime = last.getTime();
	var timeout = false;
	
	if (lastTime <= nowTime){
		if ((nowTime - lastTime) >= interval){
			timeout = true;
		}
	} else {
		timeout = true;
	}
	return timeout;
}

//   0 = 0:0
function second2ShortHour(second) {
	var hour = parseIntDecimal(second / 3600);
	var hourStr = hour;
	if (hour < 10) {
		hourStr = "0" + hour;
	}
	var minute = parseIntDecimal((second % 3600) / 60);
	var minStr = minute;
	if (minute < 10) {
		minStr = "0" + minute;
	}
	return hourStr + ":" + minStr;
}

//   0 = 0:0
function second2ShortHourEx(second) {
	var hour = parseIntDecimal(second / 3600);
	var hourStr = hour;
	if (hour < 10) {
		hourStr = "0" + hour;
	}
	var minute = parseIntDecimal((second % 3600) / 60);
	var minStr = minute;
	if (minute < 10) {
		minStr = "0" + minute;
	}
	var second = parseIntDecimal(second %  60);
	var secStr = second;
	if (second < 10) {
		secStr = "0" + second;
	}
	return hourStr + ":" + minStr + ":" + secStr;
}

export function parseIntDecimal(str) {
	return parseInt(str, 10);
}

//0:0  0
function shortHour2Second(hour) {
	var temp = hour.split(":");
	if (temp.length == 2) {
		return parseIntDecimal(temp[0]) * 3600 + parseIntDecimal(temp[1]) * 60;
	} else if (temp.length == 3) {
		return parseIntDecimal(temp[0]) * 3600 + parseIntDecimal(temp[1]) * 60 + parseIntDecimal(temp[2]);
	} else {
		return 0;
	}
}

//
function getCountDays(day) {
    var curDate = day;
    /*  */
    var curMonth = curDate.getMonth();
   /*  : curMonth1, 1 */
   curDate.setMonth(curMonth + 1);
   /* 0, , ,  */
   curDate.setDate(0);
   /*  */
   return curDate.getDate();
}

