    },
    addNetworkMarker() {
      if (this.mapvLayer) {
        this.mapvLayer.destroy();
      }
      if (this.mapvLayer1) {
        this.mapvLayer1.destroy();
      }
      let geom = [], geomDataNetwork = [], geomDataNetwork1 = [];
      //
        let networks=[];
        if(this.carTodayNetworks.hasOwnProperty(this.mdForm.imeiCar)){
        networks=this.carTodayNetworks[this.mdForm.imeiCar];
        }
       
        for (let j = 0; j < networks.length; j++) {
          if(this.networkObj.hasOwnProperty(networks[j])){
            geom =this.networkObj[networks[j]].geom.slice(6, -1).split(" ")
            geomDataNetwork.push(
              {
                geometry: {
                  type: 'Point',
                  coordinates: [geom[0], geom[1]]
                },
                text: this.networkObj[networks[j]].name
              })
            geomDataNetwork1.push(
              {
                geometry: {
                  type: 'Point',
                  coordinates: [geom[0], geom[1]]
                }

              })
          }
          
        }
      let dataSet = new mapv.DataSet(geomDataNetwork);
      let dataSet1 = new mapv.DataSet(geomDataNetwork1);
      let options = {
        draw: 'text',
        avoid: true,
        size: 12,
        font: '18px Arial',
        fillStyle: '#31478F',
        shadowColor: '#31478F',
        shadowBlur: 10
      }
      this.mapvLayer = new mapv.baiduMapLayer(this.map, dataSet, options);
      let options1 = {
        fillStyle: 'rgba(255, 50, 50, 1)',
        shadowColor: 'rgba(255, 50, 50, 1)',
        shadowBlur: 30,
        globalCompositeOperation: 'lighter',
        size: 5,
        draw: 'simple'
      }
      this.mapvLayer1 = new mapv.baiduMapLayer(this.map, dataSet1, options1);

    },
    selectChange() {
      if (this.mapvLayer) {
        this.mapvLayer.destroy();
      }
      if (this.mapvLayer1) {
        this.mapvLayer1.destroy();
      }
      let geom = [], geomDataNetwork = [], geomDataNetwork1 = [];
      //
      for (let i = 0; i < this.RNvalue.length; i++) {
        for (let j = 0; j < this.networkOptions.length; j++) {
          if (this.RNvalue[i][1].split('-')[1] == this.networkOptions[j].id) {
            geom = this.networkOptions[j].geom.slice(6, -1).split(" ")
            geomDataNetwork.push(
              {
                geometry: {
                  type: 'Point',
                  coordinates: [geom[0], geom[1]]
                },
                text: this.networkOptions[j].name
              })
            geomDataNetwork1.push(
              {
                geometry: {
                  type: 'Point',
                  coordinates: [geom[0], geom[1]]
                }

              })
          }
        }

      }



      let dataSet = new mapv.DataSet(geomDataNetwork);
      let dataSet1 = new mapv.DataSet(geomDataNetwork1);
      let options = {
        draw: 'text',
        avoid: true,
        size: 12,
        font: '18px Arial',
        fillStyle: '#31478F',
        shadowColor: '#31478F',
        shadowBlur: 10
      }
      this.mapvLayer = new mapv.baiduMapLayer(this.map, dataSet, options);
      let options1 = {
        fillStyle: 'rgba(255, 50, 50, 1)',
        shadowColor: 'rgba(255, 50, 50, 1)',
        shadowBlur: 30,
        globalCompositeOperation: 'lighter',
        size: 5,
        draw: 'simple'
      }
      this.mapvLayer1 = new mapv.baiduMapLayer(this.map, dataSet1, options1);

    },
    getRouteAndNetwork(){
        this.RNoptions=[];let belongRouteNetwork=[];
        let p1=new Promise((resolve,reject)=>{
        mapSelect().then(res => {
          if (res.data == null) {
            res.data=[]
          }
          resolve(res.data);
        }).catch(()=>{
          reject();
        });
        })
        let p2=new Promise((resolve,reject)=>{
        allDestination({ type: -1 }).then(res => {
          if (res.data.records === null) {
            res.data=[];
          }
          this.networkOptions=res.data;
          resolve(res.data);
        }).catch(()=>{
          reject();
        });
        });
        Promise.all([p1,p2]).then((data)=>{
        data[0].forEach((item,index)=>{
          let obj={value:item.id,label:item.name,children:[]};
          data[1].forEach((item1,index1)=>{
          if(JSON.parse(item.crosslobby).indexOf(item1.id)!==-1 ){
          obj.children.push({value:item.id+'-'+item1.id,label:item1.name});
          //
          if(belongRouteNetwork.indexOf(item1.id)==-1){
          belongRouteNetwork.push(item1.id)
          }
          }
          }) 
          this.RNoptions.push(obj);
        })
        //
        let notBelongRouteNetwork=[];
        data[1].forEach((item,index)=>{
        if(belongRouteNetwork.indexOf(item.id)==-1){
        notBelongRouteNetwork.push({value:0+'-'+item.id,label:item.name})
        }
        })
        //
        if(notBelongRouteNetwork.length){
        this.RNoptions.push({value:0,label:'',children:notBelongRouteNetwork})
        }

         
        })
    },
    handleAddNetwork(e) {
      this.addNetworkVisible = true;
      this.addNetworkTemp.lng = e.point.lng;
      this.addNetworkTemp.lat = e.point.lat;
      this.handleAddress(e.point).then((res) => {
        this.addNetworkTemp.networkSearchVal = res;
      })
    },

