    //tree
    filterNode(value, data, node) {
      value = value.trim();
      if (
        !value ||
        data.name.indexOf(value) !== -1 ||
        (data.serialNo && data.serialNo.indexOf(value) !== -1)
      )
        return true;
      // 
      return this.checkBelongToChooseNode(value, data, node);
    },
    // 
    checkBelongToChooseNode(value, data, node) {
      const level = node.level;
      // 
      if (level === 1) return false;
      // 
      let parentData = node.parent;
      // 
      let index = 0;
      while (index < level - 1) {
        // 
        if (
          parentData.data.name.indexOf(value) !== -1 ||
          (parentData.data.serialNo &&
            parentData.data.serialNo.indexOf(value) !== -1)
        ) {
          return true;
        }
        // 
        parentData = parentData.parent;
        index++;
      }
      // false
      return false;
    },
