    //
    processFileDay(data, i) {
      //	data.year = 15;
      //	data.beg = 43200;
      //	data.end = 86000;
      // 
      //
      data.yearMonthDay = this.videoDate;
      var day = Number(data.yearMonthDay.substring(8, 10));
      //	data.day = day - 1;
      //	data.end = 86400 + 1500;
      var fileDay = Number(data.day);
      var fileRealDate = this.getFileTime(data.year, data.mon, data.day);
      if (
        !dateCompareStrDateRange(data.yearMonthDay, fileRealDate, 1) ||
        !dateCompareStrDateRange(fileRealDate, data.yearMonthDay, 1)
      ) {
        // this.loadTimeLine = false;

        data.relBeg = data.beg;
        data.relEnd = data.end;
        data.beginDate = fileRealDate + " " + this.second2ShortHourEx(data.beg);
        data.endDate = fileRealDate + " " + this.second2ShortHourEx(data.end);
        data.timeTitle = data.beginDate;
        // if (this.queryParam.ftp != 1) {//1 5
        data.timeTitle += " - " + this.second2ShortHourEx(data.end);
        // }
      } else {
        // this.loadTimeLine = true;
        //
        if (fileDay < day || (day == 1 && fileDay <= 31 && fileDay >= 28)) {
          data.relBeg = 0;
          data.relEnd = Number(data.end) - 86400;
          data.beginDate =
            fileRealDate + " " + this.second2ShortHourEx(data.beg);
          data.endDate =
            dateFormat2DateString(
              dateGetNextMulDay(dateStrLongTime2Date(data.beginDate), 1)
            ) +
            " " +
            this.second2ShortHourEx(data.relEnd);
          data.timeTitle = data.beginDate;
          // if (this.queryParam.ftp != 1) {
          data.timeTitle += " - " + data.endDate;
          // }
        } else if (fileDay == day && Number(data.end) > 86400) {
          //
          data.relBeg = data.beg;
          data.relEnd = 86399;
          data.beginDate =
            fileRealDate + " " + this.second2ShortHourEx(data.beg);
          data.endDate =
            dateFormat2DateString(
              dateGetNextMulDay(dateStrLongTime2Date(data.beginDate), 1)
            ) +
            " " +
            this.second2ShortHourEx(Number(data.end) - 86400);
          data.timeTitle = data.beginDate;
          // if (this.queryParam.ftp != 1) {
          data.timeTitle += " - " + data.endDate;
          // }
        } else {
          data.relBeg = data.beg;
          data.relEnd = data.end;
          data.beginDate =
            fileRealDate + " " + this.second2ShortHourEx(data.beg);
          data.endDate = fileRealDate + " " + this.second2ShortHourEx(data.end);
          data.timeTitle = data.beginDate;
          // if (this.queryParam.ftp != 1) {
          data.timeTitle += " - " + this.second2ShortHourEx(data.end);
          // }
        }
      }
      this.videoList[i] = data;
