    handleCloseDownload(done) {
      this.$confirm("")
        .then(_ => {
          done();
        })
        .catch(_ => { });
    },
    //
    downloadSave() {
      this.dialogVisibleDownload = false;
      this.QueryServer("down", this.downloadRow);
    },
    renderContent(h, { node, data, store }) {
      if (data.type == "car") {
        return (
