    },
    //tree
    formatData(res, type) {
      var that = this;

      var teamList = [];

      that.options = [];

      //
      for (var item in res.data) {
        if (item == "") { continue }
        var obj = {};
        obj.id = res.data[item][0].serialNo;
        if (res.data[item][0].teamName == "") {
          obj.name = "";
          obj.label = res.data[item][0].teamName;
        } else {
          obj.name = res.data[item][0].teamName;
          obj.label = res.data[item][0].teamName;
        }

        teamList.push(obj);
        // console.log(obj);
      }
      // console.log(teamList);
      for (var i = 0; i < teamList.length; i++) {
        teamList[i].children = [];
        //
        for (var j = 0; j < res.data[teamList[i].label].length; j++) {
          if (res.data[teamList[i].label][j].serialNo == "") { continue };
          var obj1 = {};
          obj1.id = res.data[teamList[i].label][j].serialNo;
          obj1.serialNo = res.data[teamList[i].label][j].serialNo;
          obj1.name = res.data[teamList[i].label][j].licenseNo;
          obj1.label = res.data[teamList[i].label][j].carName;
          // if (res.data[teamList[i].label][j].runType == 1) {
          //   obj1.className = "iconfont iconche1";
          // } else if (res.data[teamList[i].label][j].runType == 0) {
          //   obj1.className = "iconfont iconche1";
          // }
          obj1.type = "car";
          obj1.children = [];
          obj1.total = res.data[teamList[i].label][j].channelNumber;
          obj1.channelName = res.data[teamList[i].label][j].channelName;
          teamList[i].children.push(obj1);
          if (
            res.data[teamList[i].label][j].channelNumber !== -1 &&
            res.data[teamList[i].label][j].channelNumber !== 0
          ) {
            for (
              var k = 0;
              k < res.data[teamList[i].label][j].channelNumber;
              k++
            ) {
              var obj2 = {
                id: k,
                name: "" + (k + 1),
                type: "camera",
                index: k,
                isHiddenSelect: true,
                parentId: res.data[teamList[i].label][j].serialNo,
                licenseNo: res.data[teamList[i].label][j].licenseNo,
                total: res.data[teamList[i].label][j].channelNumber,
                channelName: res.data[teamList[i].label][j].channelName
              };
              teamList[i].children[j].children.push(obj2);
            }
          }

          //option
          var optionsObj = {};
          optionsObj.label = res.data[teamList[i].label][j].licenseNo;
          optionsObj.value = res.data[teamList[i].label][j].serialNo;
          optionsObj.channelName = res.data[teamList[i].label][j].channelName;
          optionsObj.total = res.data[teamList[i].label][j].channelNumber;

          that.options.push(optionsObj);
        }
      }
      teamList.sort(function (a, b) {
        return b.children.length - a.children.length;
      });

      teamList = this.teamSort(teamList, this.sortedTeam);
      that.data = [
        {
          id: "1",
          name: this.company + "()",
          label: " 1",
          children: teamList
        }
      ];

      // console.log(that.data);
      // console.log(teamList);
      // console.log(that.options);
    },
    teamSort(list, cur) {
      let arr = list;
      let push = [];
      let otherTeam = [];
      for (let x in arr) {
        if (this.inArray(arr[x].name, cur)) {
          arr[x]["sort"] = cur.indexOf(arr[x].name);
          push.push(arr[x]);
        } else {
          otherTeam.push(arr[x]);
        }
      }
      push = push.sort(function (a, b) {
        if (a.sort < b.sort) {
          return -1;
        } else if (a.sort > b.sort) {
          return 1;
        }
        return 0;
      });
      return push.concat(otherTeam);
    },
    inArray(search, array) {
      for (let i in array) {
        if (array[i] == search) {
          return true;
        }
      }
      return false;
    },
