    //  
    mdSearch(formName) {
      // console.warn(this.mdForm)
      let _t = this,
        _s = new Date(_t.mdForm.date.replace(/-/g, '/') + ' ' + _t.mdForm.timeS).getTime(),// /MacOS
        _e = new Date(_t.mdForm.date.replace(/-/g, '/') + ' ' + _t.mdForm.timeE).getTime();
      _t.$refs[formName].validate((valid) => {
        if (valid) {
          if (_s > _e) {
            _t.$message.warning("");
          } else {
            _t.mdInit();// 
            // let carsLength = _t.mdForm.imeiCar.length/2,// 0  201
            // 
            let carsLength = !!_t.mdForm.imeiCar ? 1 : 0,
              gunsLength = _t.mdForm.imeiGun.length,
              listLength = carsLength + gunsLength,// 
              stopLoadingLength = 0,// 
              allData = [];// 
            // 
            function gameOver() {
              if (++stopLoadingLength == listLength) {// 
                _t.mdLoad = false;// 
                if (allData.length > 0) {
                  _t.mdContext.forEach(x => {// 
                    x.mdStartAnimate();
                  });
                  _t.map.setViewport(allData, { margins: [100, 100, 100, 100] }); // 
                  // console.warn('',_t.pointDetails)
                  // console.warn('',_t.mdClickPointCollection)
                  // console.warn('mdContext',_t.mdContext)
                  // 
                  _t.mdClickPointCollection.mdPC = new BMap.PointCollection([], { size: BMAP_POINT_SIZE_SMALL, color: 'rgba(0,0,0,0)' });
                  _t.map.addOverlay(_t.mdClickPointCollection.mdPC);
                  _t.mdClickPointCollection.data = allData;// 
                  _t.shPoint && _t.mdClickPointCollection.mdPC.setPoints(allData);// 
                  // 
                  _t.mdClickPointCollection.mdPC.addEventListener('click', function (e) {
                    // setTimeout(()=>{// 
                    //   _t.map.setViewport([e.point]);
                    // },0);
                    let getRes = _t.pointDetails[`p${e.point.lng},${e.point.lat}`],
                      cont = Object.values(getRes),// 
                      content = '';// 
                    for (let i of cont) {
                      content += _t.dealData(i);
                      _t.mdContext.map(x => {// imei
                        if (x.imei == i.imei) {
                          x.cont = _t.dealData(i);
                        };
                      });
                    };
                    if (content) {
