        solidData = [],// 
        dottedData = [],// 
        solidIndex = 0,// 
        dottedIndex = 0,// 
        extraArr = [];// 
      let _s = new Date(_t.mdForm.date.replace(/-/g, '/') + ' ' + _t.mdForm.timeS).getTime(),// /MacOS
        _e = new Date(_t.mdForm.date.replace(/-/g, '/') + ' ' + _t.mdForm.timeE).getTime(),
        timer = (_e - _s) / 1000,// 
        timeRangeObj = {};// 

      function parseDom(arg) {
        var objE = document.createElement("div");
        objE.innerHTML = arg;
        return objE;
