          };
          _t.pointDetails[`p${point.lng},${point.lat}`][`imei${x.imei}`] = x;
          // 3min
          if (i < toData.length - 1) {// 3
            if (new Date(toData[i + 1].updateTime).getTime() - new Date(x.updateTime).getTime() > 180000) {// 3
              // 
              extraArr.push(point);
              solidData[solidIndex++] = extraArr;
              extraArr = [];
              // 
              let dotPoint = Gps2Bd(new BMap.Point(toData[i + 1].lng, toData[i + 1].lat));
              dotPoint.lng = +(+dotPoint.lng).toFixed(6);
              dotPoint.lat = +(+dotPoint.lat).toFixed(6);
              dottedData[dottedIndex++] = [point, dotPoint];
            } else {
              extraArr.push(point);
              solidData[solidIndex] = extraArr;
            };
          } else {// 
            extraArr.push(point);
            solidData[solidIndex] = extraArr;
          };
          timeRangeObj[`x${x.timeranges}`] = x;
        });
