        richMarker.getContent().parentNode.style.background = "transparent";// 
        map.addOverlay(mark);
        _t.marks.push(mark)
        mark.setZIndex(0);// 
        let mdObject = {// 
          type: obj.type,// 
          origData: toData,// 
          data: obj.mapData,// 
          cont: '',// 
          progress: {// /// value
            value: 0,
            // max:toData.length-1,
            max: timer,
            startOrpause: true,// true
            times: []// 
          },
          isFollow: true,// 
          speed: 50,// 
