            // return new Date( _s+e*1000 ).toTimeString().slice(0,8)+'------'+e;// e==timerangestimerangesindex
          },
          mdSetPosition: function () {// ,,
            // 
            let arrIndex = 0;// 
            if (mdObject.progress.value > toData[toData.length - 1].timeranges) {// timerange,
              arrIndex = toData[toData.length - 1].index - 1;
            } else {
              if (timeRangeObj[`x${mdObject.progress.value}`]) {// index
                arrIndex = timeRangeObj[`x${mdObject.progress.value}`].index - 1;
              } else {// 
                for (let i = (mdObject.progress.value); i >= 0; i--) {
                  if (timeRangeObj[`x${i}`]) {
                    arrIndex = timeRangeObj[`x${i}`].index - 1;
                    break;
                  }
                };
              };
            };
            mark.setPosition(mdObject.data[arrIndex]);// 
