            // mdObject.isFollow && richMarker.setPosition(obj.mapData[mdObject.progress.value]);
          },
          mdStartAnimate: function () {// 
            if (mdObject.progress.startOrpause) {// 
              mdObject.progress.startOrpause = false;// 
              if (mdObject.progress.value >= mdObject.progress.max) {// 0
                mdObject.progress.value = 0;
              };
              let setT = setInterval(() => {// 
                if (++mdObject.progress.value > mdObject.progress.max) {// 
                  mdObject.mdStopAnimate();
                };
              }, mdObject.speed);
              mdObject.progress.times.push(setT);
            } else {// 
              mdObject.mdStopAnimate();
            };
          },
          mdStopAnimate: function (stop) {// 
            for (let i of mdObject.progress.times) {// 
              clearInterval(i);
            };
            mdObject.progress.times = [];// 
            mdObject.progress.startOrpause = true;// 
            if (stop) {// 
              mdObject.progress.value = mdObject.progress.max;
            };
          },
