              } else if (sortingType == "ascending") {//
                mdObject.pageData.oriData = mdObject.pageData.oriData.sort((a, b) => a[fieldName] - b[fieldName]);
              } else {
                mdObject.pageData.oriData = [...toData];
              }
            },
            data: function () {// 
              let s = (mdObject.pageData.page - 1) * mdObject.pageData.limit, e = s + mdObject.pageData.limit;
              return mdObject.pageData.oriData.slice(s, e);
              // return toData.slice();// 
            },// 
            getPages: function () {// 0
              let tableBody = document.querySelector(`.t${imeis} .el-table__body-wrapper.is-scrolling-none`);
              if (tableBody) {
                tableBody.scrollTop = 0;
              }
              // for(let i=0;i<mdObject.pageData.data().length;i++){
              //   let mes=mdObject.pageData.data()
              //    if(mes[i].speed==0){
              //    _t.handleAddress(new BMap.Point(mes[i].lng, mes[i].lat)).then((res)=>{
              //   _t.$set(mes[i],'address',res);

              // })
              // }
              // }
            },

          },

        };




        _t.mdContext_list.push(mdObject);// 
        // console.log(_t.mdContext)
        // 
        obj.data = toData;
      };
      return obj.mapData;// 
    },
    // dealData(data){// 
    //   return `<ul class="myInfo">
    //     <li><i class="eid">${data.imei}</i></li>
    //     <li ${data.type == 'car'?'class="hide"':''}><i>${data.battery} %</i></li>
    //     <li ${(data.type == 'car' || this.power!=1)?'class="hide"':''}><i>${data.signalPower}</i></li>
    //     <li><i>${data.directionCont}</i></li>
    //     <li><i>${data.lngandlat.lng} , ${data.lngandlat.lat}</i></li>
    //     <li><i>${data.speed} Km/h</i></li>
    //     <li><i>${data.gt}</i></li>
    //   </ul>`;
    // },
    dealData(data) {// 
      // console.warn(data)
      return `<h5 class="l-tit">${data.imei}</h5>
        <ul class="myInfo ${data.type}">
        <li ${data.type == 'car' ? 'class="hide"' : ''}><span></span><i>${data.battery == -1 || !data.battery ? '--' : (data.battery + ' %')}</i></li>
        <li ${(data.type == 'car' || this.power != 1) ? 'class="hide"' : ''}><span></span><i>${data.signalPower}</i></li>
        <li><span> </span><i>${data.directionCont}</i></li>
        <li><span> </span><i>${data.lngandlat.lng} , ${data.lngandlat.lat}</i></li>
        <li><span> </span><i>${data.speed} Km/h</i></li>
        <li><span> </span><i>${data.gt}</i></li>
      </ul>`;
    },
    //
    deleteSomeOverlays(){
    this.startMarkers.forEach((item,index)=>{
    this.map.removeOverlay(item);
    })
    this.endMarkers.forEach((item,index)=>{
    this.map.removeOverlay(item);
    })
     this.solidPolylines.forEach((item,index)=>{
    this.map.removeOverlay(item);
    })
     this.dotPolylines.forEach((item,index)=>{
    this.map.removeOverlay(item);
    })
     this.mdPointCollection.forEach((item,index)=>{
    this.map.removeOverlay(item.mdPC);
    })
     this.mdBPointCollection.forEach((item,index)=>{
    this.map.removeOverlay(item.mdPC);
    })
     this.richMarkers.forEach((item,index)=>{
    this.map.removeOverlay(item);
    })
     this.marks.forEach((item,index)=>{
    this.map.removeOverlay(item);
    })
    this.map.removeOverlay(this.mdClickPointCollection.mdPC);
    this.startMarkers=[];
    this.endMarkers=[];
    this.solidPolylines=[];
    this.dotPolylines=[];
    this.mdPointCollection=[];
    this.mdBPointCollection=[];
    this.richMarkers=[];
    this.marks=[];
    this.mdClickPointCollection = {};// 
    },
    mdInit() {// 
      let _t = this;
      _t.map.reset();
      // _t.map.clearOverlays();
      // _t.map.clearOverlays()
      _t.deleteSomeOverlays();
      _t.mdLoad = true;// 
      // _t.mdPointCollection = [];// imei data
      // _t.mdBPointCollection = [];// 
      _t.mdContext = [];// 
      _t.mdContext_list = [];// 
      _t.pointDetails = {};// 
      // _t.mdClickPointCollection = {};// 
    },
    ShPoint(e) {// 
      let _t = this;
      if (e) {// 
        _t.mdPointCollection.forEach(x => {// 
          x.mdPC && x.mdPC.setPoints(x.data);
        });
        _t.mdBPointCollection.forEach(x => {// 
          x.mdPC && x.mdPC.setPoints(x.data);
        });
        _t.mdClickPointCollection.mdPC && _t.mdClickPointCollection.mdPC.setPoints(_t.mdClickPointCollection.data);// 
      } else {// 
        _t.mdPointCollection.forEach(x => {
          x.mdPC && x.mdPC.clear();
        });
        _t.mdBPointCollection.forEach(x => {
          x.mdPC && x.mdPC.clear();
        });
        _t.mdClickPointCollection.mdPC && _t.mdClickPointCollection.mdPC.clear();
      };
    },
    removeValid() {// 
      this.$refs.mdForm.clearValidate(['imeiCar', 'imeiGun']);
    },
    limitChecks(a, b, n = 2) {// n(a,b)
      let _t = this, aLen = a.length, bLen = b.length;
      // 
      // 
      if (aLen > bLen) {
        a.forEach(x => {
          if (b.indexOf(x) < 0) {
            b.push(x);
          };
        });
        if (bLen >= n) {// n
          b.shift();
        };
      } else {
        for (var i = bLen - 1; i >= 0; i--) {// 
          if (a.indexOf(b[i]) < 0) {
            b.splice(i, 1);
          };
        };
      };
      for (var i = aLen - 1; i >= 0; i--) {// 
        if (b.indexOf(a[i]) < 0) {
          delete a[i]
        };
      };
      a = a.filter(x => x);
      // a = b;// 
      return a;
    },
    poperHide() {// cascaderpoper
      this.gunPoper = false;
      this.carPoper = false;
    },
    limitOriGunChecks() {// 
      // let _t = this;
      // sessionStorage.imeiGunArr = JSON.stringify(_t.limitChecks(_t.mdForm.imeiGun,_t.limitmdFormGun));
      // _t.mdForm.imeiGun = JSON.parse(sessionStorage.imeiGunArr);
      this.dataGun.push(0);
      this.dataGun.pop();// 
      let _t = this, aLen = _t.mdForm.imeiGun.length, bLen = _t.limitmdFormGun.length;
      // 
      // 
      if (aLen > bLen) {
        _t.mdForm.imeiGun.forEach(x => {
          if (_t.limitmdFormGun.indexOf(x) < 0) {
            _t.limitmdFormGun.push(x);
          };
        });
        if (bLen >= 2) {// n
          _t.limitmdFormGun.shift();
        };
      } else {
        for (var i = bLen - 1; i >= 0; i--) {// 
          if (_t.mdForm.imeiGun.indexOf(_t.limitmdFormGun[i]) < 0) {
            _t.limitmdFormGun.splice(i, 1);
          };
        };
      };
      for (var i = aLen - 1; i >= 0; i--) {// 
        if (_t.limitmdFormGun.indexOf(_t.mdForm.imeiGun[i]) < 0) {
          delete _t.mdForm.imeiGun[i]
        };
      };
      sessionStorage.imeiGunArr = JSON.stringify(_t.mdForm.imeiGun.filter(x => x));
      _t.mdForm.imeiGun = JSON.parse(sessionStorage.imeiGunArr);
    },
    changeValue() {// 
      this.mdForm.imeiCar = '';
      this.mdForm.imeiGun = [];
    },
    findCar2gun() {// 
      let _t = this,
        _s = new Date(_t.mdForm.date.replace(/-/g, '/') + ' ' + _t.mdForm.timeS).getTime(),// /MacOS
        _e = new Date(_t.mdForm.date.replace(/-/g, '/') + ' ' + _t.mdForm.timeE).getTime();

      _t.gunPoper = true;
      _t.bindGunData = [];// 
      _t.bindGunNum = _t.mdForm.imeiGun;// 
      _t.copyGunNum = [];// 
      queryByCar({
        serialNo: _t.mdForm.imeiCar,
        startTime: _s,
        endTime: _e,
        tableDate: _t.mdForm.date.replace(/-/g, '_')
      }).then(res => {// dataGun
        let result = {};
        if (res.success && res.data) {
          res.data.forEach(x => {
            result[x.gunTeamName] = [];
          });
          res.data.forEach(x => {
            result[x.gunTeamName].push({ iMei: x.imei, code: x.code });
          });
          // console.log('result',result)
          _t.bindGunData = _t.formatCas(result, 'iMei', 'code');// 
        };
      });
    },

    findGun2car() {// 
      let _t = this,
        _s = new Date(_t.mdForm.date.replace(/-/g, '/') + ' ' + _t.mdForm.timeS).getTime(),// /MacOS
        _e = new Date(_t.mdForm.date.replace(/-/g, '/') + ' ' + _t.mdForm.timeE).getTime();

      let result = {},// 
        serialNoArr = [];// 
      // 
      _t.bindCarData = [];// 
      _t.bindCarNum = _t.mdForm.imeiCar;// 
      _t.carPoper = true;
      if (_t.mdForm.imeiGun.length > 0) {
        for (let i = 0; i < _t.mdForm.imeiGun.length; i++) {// 
          queryByGun({
            iMei: _t.mdForm.imeiGun[i],
            startTime: _s,
            endTime: _e,
            tableDate: _t.mdForm.date.replace(/-/g, '_')
          }).then(res => {
            // console.log(res)
            if (res.success && res.data) {
              res.data.forEach(y => {
                if (!Array.isArray(result[y.carTeamName])) {// 
                  result[y.carTeamName] = [];
                };
              });
              res.data.forEach(z => {
                if (serialNoArr.indexOf(z.serialNo) < 0) {// 
                  result[z.carTeamName].push({ serialNo: z.serialNo, licenseNo: z.licenseNo });
                  serialNoArr.push(z.serialNo);
                };
              });
            };
            if (i == _t.mdForm.imeiGun.length - 1 && Object.keys(result).length > 0) {// ,result
              console.log(result)
              _t.bindCarData = _t.formatCas(result, 'serialNo', 'licenseNo');// 
            };
          })
        };
      };
    },
    // 
    tRFindCar2Gun() {//  tRCarToGun
      let _t = this,
        _s = new Date(_t.mdForm.date.replace(/-/g, '/') + ' ' + _t.mdForm.timeS).getTime(),// /MacOS
        _e = new Date(_t.mdForm.date.replace(/-/g, '/') + ' ' + _t.mdForm.timeE).getTime();
      _t.gunPoper = true;// 
      _t.tRCarToGun = [];// 
      tRQueryByCar({
        serialNo: _t.mdForm.imeiCar,
        startTime: _s,
        endTime: _e,
        tableDate: _t.mdForm.date.replace(/-/g, '_')
      }).then(res => {
        if (res.success && res.data) {
          _t.tRCarToGun = res.data;
        }
      });
    },
    tRFindGun2Car() {// 
      let _t = this,
        _s = new Date(_t.mdForm.date.replace(/-/g, '/') + ' ' + _t.mdForm.timeS).getTime(),// /MacOS
        _e = new Date(_t.mdForm.date.replace(/-/g, '/') + ' ' + _t.mdForm.timeE).getTime();
      _t.carPoper = true;// 
      _t.tRGunToCar = [];// 
      if (_t.mdForm.imeiGun.length > 0) {
        for (let i = 0; i < _t.mdForm.imeiGun.length; i++) {
          tRQueryByGun({
            iMei: _t.mdForm.imeiGun[i],
            startTime: _s,
            endTime: _e,
            tableDate: _t.mdForm.date.replace(/-/g, '_')
          }).then(res => {
            if (res.success && res.data) {
              _t.tRGunToCar = _t.tRGunToCar.concat(res.data);
            };
          });
        };
      };
    },
    // 
    changeGunSearch(o) {// 
      // 
      this.dataGun.push(0);
      this.dataGun.pop();// 
      // if(this.mdForm.imeiGun.length>=2){// 
      //   this.mdForm.imeiGun.shift();
      // };
      // this.mdForm.imeiGun.push(o.imei);
      // 
      this.mdForm.imeiGun = [o.imei];
      this.limitmdFormGun = [o.imei];
      this.myTimeRange = [o.startHour, o.endHour];// 
    },
    changeCarSearch(o) {// 
      this.mdForm.imeiCar = o.serialNo;
      this.myTimeRange = [o.startHour, o.endHour];// 
    },
    changeTimeRange() {// 
      this.myTimeRange = [0, 23];// 
    },

    changeCarChoose(e) {// 
      this.mdForm.imeiCar = e;
    },
    changeGunChoose(e) {// 
      // console.log(e)
      let _t = this, eLen = e.length, copyLen = _t.copyGunNum.length;
      // 
      // 
      if (eLen > copyLen) {
        e.forEach(x => {
          if (_t.copyGunNum.indexOf(x) < 0) {
            _t.copyGunNum.push(x);
          };
        });
        if (copyLen >= 2) {// 
          _t.copyGunNum.shift();
        };
      } else {
        for (var i = copyLen - 1; i >= 0; i--) {// 
          if (e.indexOf(_t.copyGunNum[i]) < 0) {
            _t.copyGunNum.splice(i, 1);
          };
        };
      };
      sessionStorage.kk = JSON.stringify(_t.copyGunNum);
      _t.bindGunNum = JSON.parse(sessionStorage.kk);// 
      _t.limitmdFormGun = JSON.parse(sessionStorage.kk);
      _t.mdForm.imeiGun = JSON.parse(sessionStorage.kk);// 
    },
    lTabActiveFalse() {// 
      let _t = this;
      _t.lTabActive = false;
      _t.showVideo();// 
    },
    winChangeFunc() {// tab
      if (this.winChange == 'winC-0') {
        this.winChange = 'winC-1';
      };
    },
    winChange1Func() {// tab
      if (this.winChange1 == 'winC-0') {
        this.winChange1 = 'winC-1';
      };
    },
    toPauseCar() {// 
      this.mdContext.forEach(x => {
        if (x.progress.startOrpause == false) {// 
          x.mdStartAnimate();
        };
      })
    },
    toStartCar() {// 
      this.mdContext.forEach(x => {
        if (x.progress.startOrpause == true) {// 
          x.mdStartAnimate();
        };
      })
    }
  }
};
</script>

<style lang="scss">
$leftBarW: 240px;
