        background-color: rgba(54, 54, 54, 0.3);
      }
      .menuShow {
        width: 60px;
        height: 60px;
        font-size: 12px;
        box-shadow: 0 0 5px #ccc;
        border-radius: 50%;
        background-color: rgba(42, 115, 211, 0.7);
        position: fixed;
        right: 10px;
        bottom: 10px;
        z-index: 11;
        display: flex;
        justify-content: center;
        align-items: center;
        color: #fff;
        cursor: pointer;
        outline: none;
        -webkit-tap-highlight-color: transparent;
        transition: all 1s;
        &::after {
          content: "";
          position: absolute;
          left: 0;
          top: 0;
          right: 0;
          bottom: 0;
          z-index: -1;
        }
        &.rot {
          transform: rotate(360deg);
          background-color: rgba(211, 42, 84, 0.7);
        }
      }
      .content {
        position: fixed;
        left: 0;
        top: 50px;
        right: 0;
        bottom: 0;
        z-index: 9;
        #map {
          height: calc(100vh - 50px) !important;
          width: 100vw !important;
        }
        .findTrajectory {
          // 
          width: 100%;
          left: 0;
          // transform: translateY(100%);
          transition: all 0.5s;
          .tools {
            .el-form-item__label {
              line-height: 20px;
            }
          }
          .mdElForm,
          .telSearch {
            // 
            display: none;
          }
          &.toshow {
            // transform: translateY(0);
            .mdElForm {
              display: block;
            }
            .telSearch {
              display: flex;
            }
          }
          &.changeBottom {
            bottom: 50px;
          }
          .progressBarBox {
            bottom: 100%;
            margin-bottom: 20px;
            left: 20px;
            right: 85px;
            width: auto;
            z-index: 11;
            box-shadow: 0 0 5px #ccc;
          }
        }
      } // .content
      .multideviceRes {
        // 
        left: 5px;
        right: 5px;
        transform: translateX(0);
      }
      #newLayout {
        top: 50px;
        .shrink {
